body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/window10.c5506ed8.jpg);
  background-size: 100%;
  background-position: center;
  background-attachment: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.flex-start {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.flex-center-coloum {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.flex-start-start {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.d-none {
  visibility: hidden;
  height: 0;
  width: 0;
  opacity: 0;
}
.c-white {
  font-size: 55px;
  color: white;
  font-weight: 300;
}
.bottom-bar {
    position: fixed;
    bottom: 0;
    width: 100%;
    margin: 0;
    height: auto;
    background-color: #040d11;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    z-index: 99;
}
.searchinp {
    height: 36px;
    width: 320px;
    margin: 0;
    border: none;
    background-color: #323333;
    padding: 1px 12px;
    font-size: 15px;
    margin-left: 1px;
    margin-right: 1px;
}
.icom-img {
    height: 38px;
}
.icom-img:hover {
    opacity: 0.5;
}
.icom-img-right {
    height: 24px;
}
.icom-img-right:hover {
    opacity: 0.5;
}
.time {
    text-align: center;
    color: white;
    font-size: 13px;
    padding: 2px 8px;
}
.startup-container {
    background-color: #191919;
    height: 500px;
    position: absolute;
    bottom: 34px;
    padding: 15px 10px;
    display: none;
}
.left-sidebar {
    margin: 0;
    width: 250px;
    color: white;
    margin-right: 10px;
    position: relative;
}
.right-sidebar {
    margin: 0;
    color: white;
    margin-left: 15px;
}
.width-100 {
    height: 100px;
    width: 100px;
    margin: 2px;
    border: 1px solid transparent;
    -webkit-transition: 100ms;
    transition: 100ms;
}
.width-100:hover {
    border: 1px solid white;
}
.width-200 {
    height: 100px;
    width: 205px;
    margin: 2px;
    -webkit-transition: 100ms;
    transition: 100ms;
    border: 1px solid transparent;
}
.width-200:hover {
    border: 1px solid white;
}
.pm-0 {
    padding: 0px;
    margin: 0px;
}
.ml-20 {
    margin-left: 20px; 
}
.p-5 {
    padding: 5px;
    margin: 2px;
}
.font-14 {
    font-size: 14px;
}
.font-12 {
    font-size: 10px;
    margin: 10px 0px;
}
.user-logo {
    height: 30px;
    margin: 3px 0px;
    border-radius: 100%;
    margin-right: 15px;

}
.used-img {
    height: 30px;
    width: 30px;
    margin: 3px 0px;
    margin-right: 15px;
}
.hov-effect {
    background-color: transparent;
    -webkit-transition: 400ms;
    transition: 400ms;
    cursor: pointer;
}
.hov-effect:hover {
    background-color: #d2d1d117;
}
.pad-20 {
    padding: 20px;
}
.d-flex {
    display: flex !important;
}
.user-login-container {
    background-image: url(/static/media/window10.c5506ed8.jpg);
    background-size: 100%;
    background-position: center;
    background-attachment: fixed;
    width: 100%;
    position: fixed;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    -webkit-transition: 500ms;
    transition: 500ms;
}
.login-user-img {
    height: 200px;
    width: 200px;
    border-radius: 100%;
}
.signin-btn {
    padding: 7px 35px;
    border: 1px solid transparent;
    background-color: #656565de;
    color: white;
    font-size: 15px;
    -webkit-transition: 500ms;
    transition: 500ms;
}
.signin-btn:hover {
    border: 1px solid white;
}
.login-container {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;  
    -webkit-transition: 500ms;  
    transition: 500ms;
}
.ls-img {
    width: 100%;
    height: 100%;
}
.main-screen {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;  
    -webkit-transition: 500ms;  
    transition: 500ms;
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader-img {
    height: 35px;
    margin-top: 75px;
}
